import { Xmark } from "../Xmark";
import { Checkmark } from "../Checkmark";
import React, { useEffect, useState } from "react";

export const Serie = ({ id, exerciseName, numReps }) => {
  const [name, setName] = useState('');
  const [comments, setComments] = useState(0)
  const [repes, setRepes] = useState([])

  useEffect(() => {
    setName(exerciseName);
    setRepes(Array.from(Array(numReps).keys()));
  }, [name, repes]);

  return (
    <Table>
    <thead>
          <tr>
            <th>#</th>
            <th>weight</th>
            <th>#repetitions</th>
          </tr>
        </thead>
    <tbody>
      {repes.map( (r) => (<Serie id={r} pNum={3} pWeigth={100}></Serie>))}
    </tbody>
    </Table>
  );
};